<template>
    <v-card elevation="0" :disabled="loading" :loading="loading">
      <BlockItem>
        <template v-slot:icon>
          mdi-phone
        </template>
        <template v-slot:title>
          تماس با ما
        </template>

        <v-form ref="form" >
          <v-row class="px-3" >
            <v-col class="py-0" cols="12" md="3" >
              <v-text-field
                  outlined
                  dense
                  @keypress="isNumber($event)"
                  :rules="[$store.state.rules.min(data.phone,'شماره تماس',10)]"
                  v-model="data.phone"
                  append-icon="mdi-phone-hangup-outline"
                  label="تلفن"
                  color="txtf"
              >

              </v-text-field>
            </v-col>
            <v-col class="py-0" cols="12" md="3" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.email(data.email),$store.state.rules.required(data.email,'ایمیل')]"
                  v-model="data.email"
                  append-icon="mdi-email-open-outline"
                  label="ایمیل"
                  color="txtf"
              >

              </v-text-field>
            </v-col>
            <v-col class="py-0" cols="12" md="6" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.required(data.address,'نشانی')]"
                  v-model="data.address"
                  append-icon="mdi-map-marker-outline"
                  label="نشانی"
                  color="txtf"
              >

              </v-text-field>
            </v-col>
            <v-col class="py-0" cols="12" md="3" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.required(data.linkedin_link,'آی دی اینستاگرام')]"
                  v-model="data.linkedin_link"
                  append-icon="mdi-instagram"
                  label="اینستاگرام"
                  color="txtf"
              >

              </v-text-field>
            </v-col>
            <v-col class="py-0" cols="12" md="3" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.required(data.tel_link,'آی دی تلگرام')]"
                  v-model="data.tel_link"
                  append-icon="mdi-at"
                  label="تلگرام"
                  color="txtf"
              >

              </v-text-field>
            </v-col>
            <v-col class="py-0" cols="12" md="3" >
              <v-text-field
                  outlined
                  dense
                  :rules="[$store.state.rules.required(data.website_link,'آی دی فیس بوک')]"
                  v-model="data.website_link"
                  append-icon="mdi-facebook"
                  label="فیس بوک"
                  color="txtf"
              >

              </v-text-field>
            </v-col>
            <v-col class="py-0" cols="12" md="3" >
              <v-btn class="dash-btns dash-btns--blue d-block mx-auto" :loading="loading" dark height="40" @click="updateF()" >ثبت تغییرات</v-btn>
            </v-col>
          </v-row>
        </v-form>

      </BlockItem>
      <template v-slot:progress>
        <GDLoading/>
      </template>
    </v-card>
</template>

<script>
import BlockItem from "@/components/Misc/BlockItem";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import {isChars} from "@/components/Misc/isChars";
import axios from "axios";

export default {
  name: "ContactUs",
  components: {GDLoading, BlockItem},
  created() {
    this.fetchContactUs()
  },
  mixins : [isChars],
  data () {
    return {
      loading : false,
      data : {
        phone : null,
        email : null,
        address : null,
        website_link : null,
        tel_link : null,
        linkedin_link : null,
      }
    }
  },
  methods : {
    updateF(){
      this.$refs.form.validate();
      if(this.$refs.form.validate()) {
        this.loading = true;
          axios.patch(this.$store.state.host + '/website/api/v1/website/change-contact',this.data,{
            headers : {
              Authorization: `Bearer ${this.$store.state.userId}`,
            }
          }).then(()=>{
            this.notify('تغییرات ثبت شد.','info');
          }).catch(error=>{
            this.errorHandle(error.response)
          }).finally(()=>{
            this.loading = false;
          })
      }
    },
    fetchContactUs () {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/contact-us').then(response=>{
        this.data = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
      })
    }

  }
}
</script>

